@import url("https://fonts.googleapis.com/css2?family=Nunito:ital,wght@0,700;1,700&display=swap");


body {
    height: 100%;
    margin: 0;
    overflow: hidden;
}


.conteudo__geral {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 80vh;
    padding: 10px;
}

    .conteudo__geral h1 {
        align-self: flex-end;
        color: white;
        white-space: nowrap; /* Evită ca textul să se rupă pe mai multe rânduri */
        margin-bottom: 20px;
        font-size: 2vh;
    }

    .conteudo__geral img {
        display: block;
        max-width: 30%;
        max-height: 30%;
        height: auto;
        object-fit: contain;
        margin: auto;
        filter: drop-shadow(0px 4px 10px white);
    }

.container1 {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}

.container-carrossel {
    --widthItem: 150px;
    --heightItem: 100px;
    width: var(--widthItem);
    height: var(--heightItem);
    perspective: 1000px;
}

.carrossel {
    --rotatey: 0;
    font-size: 4rem;
    position: relative;
    transform: rotatey(var(--rotatey));
    transform-style: preserve-3d;
    user-select: none;
    cursor: grab;
}

.carrossel-item {
    opacity: 0.5;
    position: absolute;
    display: table-column;
    justify-content: center;
    align-items: center;
    border-radius: 5px;
    transition: opacity 0.5s;
    height: 100%;
}

    .carrossel-item:hover {
        opacity: 1;
    }

.carrossel,
.carrossel-item {
    width: 100%;
    height: 130%;
}

    .carrossel-item:nth-child(1) {
        --rotatey: 0;
        transform: rotatey(var(--rotatey)) translatez(var(--tz));
        background: linear-gradient(-229deg, #727272, #efefef);
    }

    .carrossel-item:nth-child(2) {
        --rotatey: 0;
        transform: rotatey(var(--rotatey)) translatez(var(--tz));
        background: linear-gradient(-229deg, #cfcfcf, #efefef);
    }

    .carrossel-item:nth-child(3) {
        --rotatey: 0;
        transform: rotatey(var(--rotatey)) translatez(var(--tz));
        background: linear-gradient(-229deg, #eec302, #f8f061);
    }

    .carrossel-item:nth-child(4) {
        --rotatey: 0;
        transform: rotatey(var(--rotatey)) translatez(var(--tz));
        background: linear-gradient(-229deg, #d27a30, #fe9f50);
    }

    .carrossel-item:nth-child(5) {
        --rotate: 0;
        transform: rotatey(var(--rotatey)) translatez(var(--tz));
        background: linear-gradient(-229deg, #439c54, #7bfc94);
    }

    .carrossel-item:nth-child(6) {
        --rotatey: 0;
        transform: rotatey(var(--rotatey)) translatez(var(--tz));
        background: linear-gradient(-229deg, #3f96f7, #7cb6f8);
    }

    .carrossel-item:nth-child(7) {
        --rotatey: 0;
        transform: rotatey(var(--rotatey)) translatez(var(--tz));
        background: linear-gradient(-229deg, #cf91ff, #2159ee);
    }

    .carrossel-item:nth-child(8) {
        --rotatey: 0;
        transform: rotatey(var(--rotatey)) translatez(var(--tz));
        background: linear-gradient(-229deg, #6F4E37, #795c48);
    }

    .carrossel-item:nth-child(9) {
        --rotatey: 0;
        transform: rotatey(var(--rotatey)) translatez(var(--tz));
        background: linear-gradient(-229deg, #6F4E37, #795c48);
    }

    .carrossel-item:nth-child(10) {
        --rotatey: 0;
        transform: rotatey(var(--rotatey)) translatez(var(--tz));
        background: linear-gradient(-229deg, #6F4E37, #795c48);
    }

    .carrossel-item:nth-child(11) {
        --rotatey: 0;
        transform: rotatey(var(--rotatey)) translatez(var(--tz));
        background: linear-gradient(-229deg, #000000, #171717);
    }

/* Media Queries for responsiveness */
@media screen and (max-width: 575px) {
    .conteudo__geral h1 {
        font-size: 3vw; /* Font size smaller on mobile */
    }

    .conteudo__geral img {
        max-width: 35%;
        max-height: 35%;
    }

    .container-carrossel {
        --widthItem: 80px;
        --heightItem: 60px;
    }
}

@media screen and (min-width: 576px) and (max-width: 991px) {
    .conteudo__geral h1 {
        font-size: 2.8vh;
    }

    .conteudo__geral img {
        max-width: 40%;
        max-height: 40%;
    }

    .container-carrossel {
        --widthItem: 200px;
        --heightItem: 150px;
    }
}

@media screen and (min-width: 992px) {
    .conteudo__geral h1 {
        font-size: 4vh;
    }

    .conteudo__geral img {
        max-width: 40%;
        max-height: 40%;
    }

    .container-carrossel {
        --widthItem: 250px;
        --heightItem: 200px;
    }
}
